$base-color: #063693;
$form-color: #F0F4FD;
$dark-blue: #06296C;
$green-color: #00AA63;

@media only screen and (min-width : 280px) and (max-width : 565px) {
    /*--- Mobile landscape ---*/

    body {
        display: block;
        overflow-x: hidden;
    }

    nav {

        padding: 30px 0px;
    }

    .btn-mobile {
        display: block;
        bottom: 40px;
        left: 100px;
    }

    .navbar {

        display: none;
    }

    .txt-no {
        display: none;
    }

    .colored {
        height: 30vh;
    }

    .mobile {
        position: relative;
        // transition: all 0.6s ease-in-out;
        margin: 50px 0px 0px 0px;
        width: 100%;
        height: auto;
        padding: 20px;
        background: $base-color;
        display: none;
        z-index: 1000;
    }

    .nav-logo {
        left: 30px;


    }

    .nav-links {
        display: inline-block;
        min-width: 80%;
        height: 50px;
        line-height: 50px;

        color: #fff;
        margin: 10px 0px;
        padding: 0px 5px;


    }




    .nav-menu {
        display: block;
        //    width: 100px;
        padding: 20px;
        color: #fff;
        position: absolute;
        top: 10px;
        right: 0px;
        // margin-right: -40px;
        font-size: 26px;


        img {
            filter: brightness(0) invert(1);
        }
    }

    main {

        height: 715px;
        padding-bottom: 50px;
    }

    .home-content {

        display: block;
        justify-content: none;
        margin: 30px 0px 50px 0px;

        .text-hidden {
            padding: 20px 0px;
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            margin: 40px 10px 10px 10px;
            display: block;
        }

        .home-intro {
            height: 100px;

            h1 {
                font-size: 36px;
                line-height: 48px;
                margin: 0px;
            }

            .display-box {
                visibility: hidden;
            }
        }

    }

    .home-card {
        margin: 20px 0px;
        width: 320px;
        height: 450px;
        background: #fff;
        border-radius: 10px;
        position: relative;
        z-index: 100;
    }

    //home-secondary
    .home-secondary {
        display: block;
        position: relative;
        justify-content: none;
        height: 350px;

        // margin: 100px 0px 0px 0px;

        .home-text {
            position: relative;
            top: 50px;
            right: 20px;
            margin: 70px 20px 20px 20px;
            padding-bottom: 50px;
        }

        h1 {

            color: #fff;
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 24px;
            display: inline;
            align-items: center;
        }

        p {
            font-size: 20px;
            font-weight: light;
            color: #fff;
        }

        .home-icons {
            display: inline-flex;
            position: absolute;
            bottom: -80px;
            right: 40px;

            padding: 20px;
            height: 200px;
            padding-bottom: 60px;

        }

        .icon-box {
            cursor: pointer;
            background: #fff;
            width: 60px;
            height: 60px;
            border-radius: 20px;



            img {
                margin: 10px;
                width: 40px;
                height: 40px;

            }
        }
    }

    //what-we-do

    .what-we-do {
        display: block;
        justify-content: none;
        padding: 20px;
        margin: 0px;

        .ij-grid {
            width: auto;
            padding: 10px;


        }
    }

    //ij-trust
    .ij-trust {
        display: block;
        background: $base-color;
        width: 100%;
        height: 500px;
        position: relative;



        .cutout {
            display: none;

        }


        .ij-trust-text {
            width: 100%;
            padding: 20px;
        }

        .trust-view {
            width: 100%;
            display: block;
            margin: 10px 0px;


            .trust-images {
                width: 200px;
                height: 50px auto;
                padding: 5px;
                margin: 0px 80px;
            }

            img {
                width: 100%;
            }




        }
    }

    //..booked page ..//

    .wrapper {
        display: none;
    }

    .bookings {
        position: relative;
        top: -20px;
        margin: 0px;
        display: block;
        height: auto;
        width: 100%;
        padding: 0px;
        padding-bottom: 100px;

        .tabs {
            width: 30px;
            position: relative;
            margin: 1px 28px;

            img {
                width: 40px;
                height: 40px;
                position: absolute;
                top: 10px;
                left: -40px;
            }

            p {
                margin-left: -20px;
                margin-top: 10px;
                font-size: 16px;
                font-weight: bold;
                color: $base-color;

            }

            span {
                display: none;
                color: #999;
                font-size: 10px;
                font-weight: bold;
            }

            hr {
                width: 150%;
                display: none;
                margin: 0px !important;
            }

            .tabs:nth-child(3) hr {
                display: none;
                width: 100%;
            }

            hr.tab-active {
                display: inline;
                top: 70px;
                left: -40px;
                // height: 3px;
                background: $base-color;
            }
        }

        .tabs:focus {
            outline: none;
        }

        .tab-control {
            margin: 50px 0px;

            p {

                svg {
                    display: inline-block;
                }
            }
        }

        .booking-box {
            width: 100%;
            margin: 0px;


        }

        .booking-box div {
            width: auto;
        }
    }

    .booking-ticket {
        display: none;
    }


    .booking-time {
        position: relative;

        p {

            color: #677994;
            display: block;
            font-weight: bold;
            margin: 0px;
        }


        span {
            font-size: 9px;
            margin: 0px;

        }
    }

    .booking-box:focus {
        outline: none;
        background: transparent;
    }

    //next-page
    .home-footer {
        display: inline-block;

        .home-footer-body {
            width: 100%;
            position: absolute;
            top: 10px;
            left: 0;
            display: inline-block;

            svg {
                width: 25px;
                height: 25px;
            }

            p {
                padding: 10px 0px;
                font-size: 11px;
                color: #fff;
                font-weight: 250;
            }
        }
    }

    .next-page {
        display: block;
        padding: 0px;
        margin: 0px;
        padding: 10px 20px;

        .display-left {
            padding: 0px;
            margin: 0px;
            width: 100%;

            .card-box {
                width: 100%;

                .ticket-time {
                    margin: 30px 10px;
                }
            }
        }

        .display-right {
            padding: 0px;
            margin: 0px;
            width: 100%;

            .card-box {
                width: auto;

                .ticket-time {
                    margin: 30px 10px;
                }
            }

            .card-box {
                width: 100%;
                height: 250px auto;
                padding: 20px;
                background: #FFFFFF;
                box-shadow: 0px 2px 10px rgba(176, 176, 176, 0.25);



                .cardbox-body {

                    p {
                        width: auto;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 24px;
                        color: #3B567F;
                    }
                }
            }

        }
    }

    .btn-notRounded {
        bottom: -55rem;
        left: 50px;

    }

    .sticky {
        left: 10px;
        bottom: -10px;
    }

    .sticky-green {
        bottom: -920px;
        left: 10px;
    }

    //section-page
    .section-page {
        display: block;
        justify-content: space-evenly;
        align-items: center;
        height: auto;
        padding: 0px;
        margin: 0px;
        padding-bottom: 20px;

        .home-footer {
            display: inline-block;

            .home-footer-body {
                width: 100%;
                position: absolute;
                top: 10px;
                left: 0;
                display: inline-block;

                svg {
                    width: 25px;
                    height: 25px;
                }

                p {
                    padding: 10px 0px;
                    font-size: 11px;
                    color: #fff;
                    font-weight: 250;
                }
            }
        }

        .display-left {
            top: 0px;
            width: 100%;

            .card-box {
                width: auto;
                height: auto;
                padding: 10px 0px;
                padding-bottom: 50px;
                margin: 0px 0px 200px 0px;

                .ticket-time {
                    margin: 30px 10px;
                }

                .cardform-input {
                    width: 90%;
                }
            }
        }

        .display-right {
            width: 100%;

            .card-box {
                width: auto;

                .ticket-time {
                    margin: 30px 10px;
                }
            }

            .card-box {
                width: 100%;
                height: 250px auto;
                padding: 20px;
                background: #FFFFFF;
                box-shadow: 0px 2px 10px rgba(176, 176, 176, 0.25);



                .cardbox-body {

                    p {
                        width: auto;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 24px;
                        color: #3B567F;
                    }
                }
            }

        }
    }

    //section2-page
    .section2-page {
        display: block;
        justify-content: space-evenly;
        align-items: center;
        height: auto;
        padding: 0px;
        margin: 0px;
        padding-bottom: 200px;

        .home-footer {
            display: inline-block;

            .home-footer-body {
                width: 100%;
                position: absolute;
                top: 10px;
                left: 0;
                display: inline-block;

                svg {
                    width: 25px;
                    height: 25px;
                }

                p {
                    padding: 10px 0px;
                    font-size: 11px;
                    color: #fff;
                    font-weight: 250;
                }
            }
        }

        .display-left {
            top: 0px;
            width: 100%;

            .card-box {
                width: auto;
                height: auto;
                padding: 10px 0px;
                padding-bottom: 50px;
                margin: 0px 0px 200px 0px;

                .ticket-time {
                    margin: 30px 10px;
                }

                .cardform-input {
                    width: 90%;
                }
            }

            .card-extra {
                height: 250px;
                margin-bottom: -70px;

                .alert {
                    width: 80%;
                }
            }

            .card-kill {
                margin-bottom: -100px;
            }
        }

        .display-right {
            width: 100%;

            .card-box {
                width: auto;

                .ticket-time {
                    display: none;
                    margin: 0px 10px;
                }
            }

            .card-box {
                width: 100%;
                height: 250px auto;
                padding: 20px;
                background: #FFFFFF;
                box-shadow: 0px 2px 10px rgba(176, 176, 176, 0.25);



                .cardbox-body {

                    p {
                        width: auto;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 24px;
                        color: #3B567F;
                    }
                }
            }

        }
    }

    //discovery

    .discover {
        color: #fff;
        font-size: 24px;
        font-weight: lighter;
        text-align: center;
        margin: 100px 100px;
    }

    .discovery {
        display: none;
        width: 100%;
        height: 9vh;
        background-color: #fff;
        box-shadow: 0px 2px 10px rgba(176, 176, 176, 0.25);
        padding: 10px;
        position: absolute;
        bottom: 0;
        left: 0;

        .discovery-footer-body {

            display: none;
            align-content: center;
            vertical-align: middle;
            margin-left: 200px;


        }

        .nav-links {
            // background: #00AA63;
            padding: 10px 15px;
            width: 120px auto;
            height: 4rem;
            color: #CED8E7;
            margin: 0px 10px;


            .active {
                color: #3B567F !important;
            }

        }



        input[type=search] {

            width: 400px;
            height: 5vh;
            padding: 15px 10px;
            border: 0.30px solid #677994;
            box-sizing: border-box;
            border-radius: 34px;
            float: right;


        }


    }

    body {

        background: #E5E5E5;

        .discovery_section {
            width: auto;
            height: 1000px auto;
            display: block;
            flex-direction: none;
            padding: 10px 5px;

            .discovery_card {

                background: #fff;
                position: relative;
                float: left;
                width: 100%;

                margin: 10px 0px;
                box-shadow: 0px 2px 10px rgba(176, 176, 176, 0.25);



                .img-text {
                    display: flex;
                    justify-content: center;
                    position: absolute;
                    font-weight: lighter;
                    color: white;
                    left: 25px;
                    font-size: 24px;
                    margin-left: 10px;
                    top: 20px;
                    text-emphasis: bold;

                }

                .discovery_container {
                    padding: 16px 16px;

                    .btn-link {
                        width: 150px;
                        height: 50px;
                        border: 1px solid $dark-blue;
                        background-color: $dark-blue;
                        padding: 10px 15px;
                        border-radius: 30px;
                        text-align: center;
                        margin: 3px;
                        color: #fff;
                    }

                    .discovery-text {
                        padding: 10px 5px;
                        font-weight: lighter;
                        font-size: 16px;
                        line-height: 24px;
                    }
                }

            }

        }
    }

}











